<template>
  <div class="private_publish">
    <el-form ref="data" :model="privatePublishForm" label-position="left" label-width="160px">
      <!-- task role -->
      <el-form-item v-for="(item,index) in roleConfig" :key="item.name" :label="roleLabel[index]">
        <el-radio-group v-model="roles[index]" @change="taskRoleChange">
          <el-radio label="sole">sole</el-radio>
          <el-radio label="slack">slack</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--  target_columns -->
      <el-form-item label=":" v-if="target_columns_visible">
        <el-input v-for="(item,index) in target_columns_config" :key="item.name" v-show="item.isShow"
                  v-model="target_columns[index]" :placeholder="target_columns_PlaceHolder[index]"
                  @change="targetColumnsChange">
          <el-switch slot="append" v-model="target_columns_all[index]" @change="targetColumnsAllChange"></el-switch>
        </el-input>
      </el-form-item>
      <!--  scale_equiv_class -->
      <el-form-item label=":" v-if="scale_equiv_class_visible">
        <div v-for="(item,index) in scale_equiv_class_config" :key="item.name" v-show="item.isShow">
          <el-input v-model="scale_equiv_class[index]" :placeholder="scale_equiv_class_label[index]">
          </el-input>
          <el-radio-group v-model="scale_equiv_class[index]">
            <el-radio label="-1"></el-radio>
            <el-radio label="0"></el-radio>
          </el-radio-group>
        </div>
      </el-form-item>
      <!-- privacy_mode -->
      <div v-if="privacy_mode_visible">
        <el-form-item v-for="(item,index) in privacy_mode_config" :key="item.name"
                      :label="privacy_mode_label[index]" v-show="item.isShow">
          <el-radio-group v-model="privacy_mode[index]">
            <el-radio label="dst">dst</el-radio>
            <el-radio label="ccapt">ccapt</el-radio>
            <el-radio label="ldp">ldp</el-radio>
          </el-radio-group>
        </el-form-item>
      </div>
      <!--  privacy_level -->
      <el-form-item label=":" v-if="privacy_level_visible">
        <el-input v-for="(item,index) in privacy_level_config" :key="item.name" v-show="item.isShow" :id="item.name"
                  @change="changeInput(item.name,index,'privacy_level','checkE02E1')"
                  @focus="focusInput(item.name,index,'privacy_level','checkE02E1')"
                  @blur="blurInput(item.name,index,'privacy_level','checkE02E1')"
                  v-model="privacy_level[index]" :placeholder="privacy_level_label[index]">
        </el-input>
      </el-form-item>
      <!--  scale_low -->
      <el-form-item label=":" v-if="scale_low_visible">
        <el-input v-for="(item,index) in scale_low_config" :key="item.name" v-show="item.isShow" :id="item.name"
                  @change="changeInput(item.name,index,'scale_low','checkGL0')"
                  @focus="focusInput(item.name,index,'scale_low','checkGL0')"
                  @blur="blurInput(item.name,index,'scale_low','checkGL0')"
                  v-model="scale_low[index]" :placeholder="scale_low_label[index]">
        </el-input>
      </el-form-item>
      <!--  scale_high -->
      <el-form-item label=":" v-if="scale_high_visible">
        <el-input v-for="(item,index) in scale_high_config" :key="item.name" v-show="item.isShow" :id="item.name"
                  @change="changeInput(item.name,index,'scale_high','checkGL0')"
                  @focus="focusInput(item.name,index,'scale_high','checkGL0')"
                  @blur="blurInput(item.name,index,'scale_high','checkGL0')"
                  v-model="scale_high[index]" :placeholder="scale_high_label[index]">
        </el-input>
      </el-form-item>
      <!--  sum_preservation_group -->
      <el-form-item label=":" v-if="sum_preservation_group_visible">
        <el-input v-for="(item,index) in sum_preservation_group_config" :key="item.name" v-show="item.isShow"
                  v-model="sum_preservation_group[index]" :placeholder="sum_preservation_group_label[index]">
        </el-input>
      </el-form-item>
      <!--  prod_preservation_group -->
      <el-form-item label=":" v-if="prod_preservation_group_visible">
        <el-input v-for="(item,index) in prod_preservation_group_config" :key="item.name" v-show="item.isShow"
                  v-model="prod_preservation_group[index]" :placeholder="prod_preservation_group_label[index]">
        </el-input>
      </el-form-item>
      <!--  div_consistency_group -->
      <el-form-item label=":" v-if="div_consistency_group_visible">
        <el-input v-for="(item,index) in div_consistency_group_config" :key="item.name" v-show="item.isShow"
                  v-model="div_consistency_group[index]" :placeholder="div_consistency_group_label[index]">
        </el-input>
      </el-form-item>
      <el-form-item>
        <div style="float: right;">
          <el-button ref="addOrEditButton" type="primary" @click="addButton"></el-button>
          <el-button type="info" @click="closeDialog"></el-button>
          <el-button @click="clearForm"></el-button>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "PrivatePublish",
  props: ['data'],
  mounted() {
    //
    this.$emit('setDialogWidth','35%');
    //
    this.initPara();
    this.moduleTable = this.$global.readLocalStorage('jeddak_module_table');
    //
    let temp = null;
    if(this.$global.isEdit){
      temp = this.moduleTable[this.$global.indexOfModuleTable];
      //
      this.$refs.addOrEditButton.$el.innerText = '';
    }else{
      temp = this.$global.readLocalStorage('jeddak_privatepublish');
    }
    //
    if(temp !== null){
      if(temp.task_role !==null && temp.task_role.length===this.$global.partyNumber){
        this.parsePara(temp);
      }else{
        this.$notify.info({
          title: '',
          message: ''
        });
      }
    }
  },
  data() {
    return {
      moduleTableL:[],
      //
      roles:[],
      roleConfig:[],
      roleLabel:[],
      //
      target_columns:[],
      target_columns_config:[],
      target_columns_all:[],
      target_columns_PlaceHolder:[],
      target_columns_visible: false,
      //scale_equiv_class
      scale_equiv_class:[],
      scale_equiv_class_config:[],
      scale_equiv_class_label:[],
      scale_equiv_class_visible:false,
      //privacy_mode
      privacy_mode:[],
      privacy_mode_config:[],
      privacy_mode_label:[],
      privacy_mode_visible:false,
      //privacy_level
      privacy_level:[],
      privacy_level_config:[],
      privacy_level_label:[],
      privacy_level_visible:false,
      privacy_level_config_copy:[],
      privacy_level_label_copy:[],
      //scale_low
      scale_low:[],
      scale_low_config:[],
      scale_low_label:[],
      scale_low_visible:false,
      scale_low_config_copy:[],
      scale_low_label_copy:[],
      //scale_high
      scale_high:[],
      scale_high_config:[],
      scale_high_label:[],
      scale_high_visible:false,
      scale_high_config_copy:[],
      scale_high_label_copy:[],
      //sum_preservation_group
      sum_preservation_group:[],
      sum_preservation_group_config:[],
      sum_preservation_group_label:[],
      sum_preservation_group_visible:false,
      //prod_preservation_group
      prod_preservation_group:[],
      prod_preservation_group_config:[],
      prod_preservation_group_label:[],
      prod_preservation_group_visible:false,
      //div_consistency_group
      div_consistency_group:[],
      div_consistency_group_config:[],
      div_consistency_group_label:[],
      div_consistency_group_visible:false,
      //
      privatePublishForm:{task_type:'private_publish',task_role:[],target_columns:[],scale_equiv_class:[],
        privacy_mode:[],privacy_level:[], scale_low:[], scale_high:[], sum_preservation_group:[],
        prod_preservation_group:[], div_consistency_group:[]},
    };
  },
  methods: {
    taskRoleChange(){
      let vi = false;
      for(let i=0;i<this.$global.partyNumber;i++){
        let status = this.roles[i] === 'sole';
        vi = vi || status;
        this.target_columns_config[i].isShow = status;
        this.scale_equiv_class_config[i].isShow = status;
        this.privacy_mode_config[i].isShow = status;
        this.privacy_level_config[i].isShow = status;
        this.scale_low_config[i].isShow = status;
        this.scale_high_config[i].isShow = status;
        this.sum_preservation_group_config[i].isShow = status;
        this.prod_preservation_group_config[i].isShow = status;
        this.div_consistency_group_config[i].isShow = status;
      }
      this.target_columns_visible = vi;
      this.scale_equiv_class_visible = vi;
      this.privacy_mode_visible = vi;
      this.privacy_level_visible = vi;
      this.scale_low_visible = vi;
      this.scale_high_visible = vi;
      this.sum_preservation_group_visible = vi;
      this.prod_preservation_group_visible = vi;
      this.div_consistency_group_visible = vi;
    },
    targetColumnsAllChange(){
      for(let i=0;i<this.$global.partyNumber;i++){
        if(this.target_columns_all[i]){
          this.target_columns[i] = null;
          this.target_columns_PlaceHolder[i] = '';
        }else {
          this.target_columns_PlaceHolder[i] = '' + (i+1) + ',,1,5; ';
        }
      }
    },
    targetColumnsChange(){
      for(let i=0;i<this.$global.partyNumber;i++){
        if(this.target_columns[i] !== null){
          this.target_columns_all[i] = false;
        }
      }
      this.$forceUpdate();
    },
    //
    closeDialog(){
      this.$emit('setVisible',false);
    },
    //
    initPara(){
      // 
      let role = this.$global.formatPartyPara(this.roles, this.roleConfig, this.roleLabel,
          this.$global.partyNumber, ':','role');
      this.roles = role[0];
      this.roleConfig = role[1];
      this.roleLabel = role[2];
      // target_columns
      let columns = this.$global.formatPartyParaControlShow(this.target_columns, this.target_columns_config,
          this.target_columns_PlaceHolder, this.$global.partyNumber,
          ',,1,5; ','col')
      this.target_columns = columns[0];
      this.target_columns_config = columns[1];
      this.target_columns_PlaceHolder = columns[2];
      this.target_columns_all = this.$global.formatList(this.target_columns_all, this.$global.partyNumber);
      //  scale_equiv_class
      let scale = this.$global.formatPartyParaControlShow(this.scale_equiv_class, this.scale_equiv_class_config,
          this.scale_equiv_class_label, this.$global.partyNumber,
          ',,,1,5;2,3,4','scale')
      this.scale_equiv_class = scale[0];
      this.scale_equiv_class_config = scale[1];
      this.scale_equiv_class_label = scale[2];
      //  privacy_mode
      let mode = this.$global.formatPartyParaControlShow(this.privacy_mode, this.privacy_mode_config,
          this.privacy_mode_label, this.$global.partyNumber,
          '','mode')
      this.privacy_mode = mode[0];
      this.privacy_mode_config = mode[1];
      this.privacy_mode_label = mode[2];
      //  privacy_level
      let level = this.$global.formatPartyParaControlShow(this.privacy_level, this.privacy_level_config,
          this.privacy_level_label, this.$global.partyNumber,
          ',,[0, 1]','level')
      this.privacy_level = level[0];
      this.privacy_level_config = level[1];
      this.privacy_level_label = level[2];
      this.privacy_level_config_copy = this.$global.copyJson(this.privacy_level_config);
      this.privacy_level_label_copy = this.$global.copyJson(this.privacy_level_label);
      //  scale_low
      let low = this.$global.formatPartyParaControlShow(this.scale_low, this.scale_low_config,
          this.scale_low_label, this.$global.partyNumber,
          ',:(0, inf)','low')
      this.scale_low = low[0];
      this.scale_low_config = low[1];
      this.scale_low_label = low[2];
      this.scale_low_config_copy = this.$global.copyJson(this.scale_low_config);
      this.scale_low_label_copy = this.$global.copyJson(this.scale_low_label);
      //  scale_high
      let high = this.$global.formatPartyParaControlShow(this.scale_high, this.scale_high_config,
          this.scale_high_label, this.$global.partyNumber,
          ',:(0, inf)','high')
      this.scale_high = high[0];
      this.scale_high_config = high[1];
      this.scale_high_label = high[2];
      this.scale_high_config_copy = this.$global.copyJson(this.scale_high_config);
      this.scale_high_label_copy = this.$global.copyJson(this.scale_high_label);
      //  sum_preservation_group
      let group = this.$global.formatPartyParaControlShow(this.sum_preservation_group, this.sum_preservation_group_config,
          this.sum_preservation_group_label, this.$global.partyNumber,
          ',:1=100+101=102+103;2=203+204','group')
      this.sum_preservation_group = group[0];
      this.sum_preservation_group_config = group[1];
      this.sum_preservation_group_label = group[2];
      //  prod_preservation_group
      let prod = this.$global.formatPartyParaControlShow(this.prod_preservation_group, this.prod_preservation_group_config,
          this.prod_preservation_group_label, this.$global.partyNumber,
          ',:1=100*101=102*103;2=203*204','prod')
      this.prod_preservation_group = prod[0];
      this.prod_preservation_group_config = prod[1];
      this.prod_preservation_group_label = prod[2];
      //  div_consistency_group
      let div = this.$global.formatPartyParaControlShow(this.div_consistency_group, this.div_consistency_group_config,
          this.div_consistency_group_label, this.$global.partyNumber,
          ',:3=2/1;100=102/101','div')
      this.div_consistency_group = div[0];
      this.div_consistency_group_config = div[1];
      this.div_consistency_group_label = div[2];
    },
    //
    addButton(){
      this.summarizePara();
      this.$global.writeLocalStorage('jeddak_privatepublish',this.privatePublishForm);
      if(this.$global.isEdit){
        this.moduleTable[this.$global.indexOfModuleTable] = this.privatePublishForm;
      }else{
        this.moduleTable.push(this.privatePublishForm);
      }
      this.$global.writeLocalStorage('jeddak_module_table',this.moduleTable);
      this.closeDialog();
    },
    //
    summarizePara(){
      this.privatePublishForm={task_type:'private_publish',task_role:[],target_columns:[],scale_equiv_class:[],
        privacy_mode:[],privacy_level:[], scale_low:[], scale_high:[], sum_preservation_group:[],
        prod_preservation_group:[], div_consistency_group:[]};
      this.privatePublishForm.task_role = this.roles;
      this.privatePublishForm.privacy_mode = this.privacy_mode;
      //'privacy_level','scale_low','scale_high'
      let iter = ['privacy_level','scale_low','scale_high'];
      for(let itk=0;itk<iter.length;itk++){
        let it = iter[itk];
        if(this[it+'_visible']){
          for(let i=0;i<this.$global.partyNumber;i++){
            if(this[it+'_config'][i].isShow){
              this.privatePublishForm[it].push(parseFloat(this[it][i]));
            }else{
              this.privatePublishForm[it].push(null);
            }
          }
        }
      }
      //
      iter = ['sum_preservation_group', 'prod_preservation_group', 'div_consistency_group'];
      let segs = ['+','*','/']
      for(let itk=0;itk<iter.length;itk++){
        let it = iter[itk];
        let seg = segs[itk];
        if(this[it+'_visible']){
          for(let i=0;i<this.$global.partyNumber;i++){
            if(this[it+'_config'][i].isShow){
              if(this[it][i] === null){
                this.privatePublishForm[it].push(null);
                continue;
              }
              let equs = this[it][i].split(';');
              if(typeof(equs)=='string'){
                equs = [].push(equs);
              }
              let groups = [];
              for(let ei=0;ei<equs.length;ei++){
                let group = [];
                let equ = equs[ei].split('=');
                if(typeof(equ)=='string'){
                  equ = [].push(equ);
                }
                for(let j=0;j<equ.length;j++){
                  if(j!==0){
                    let equ2 = equ[j].split(seg);
                    equ2 = this.$global.parseStrArray2IntArray(equ2);
                    if(itk===2){
                      group = group.concat(equ2);
                    }else {
                      group.push(equ2);
                    }
                  }else {
                    group.push(parseInt(equ[j]));
                  }
                }
                groups.push(group);
              }
              this.privatePublishForm[it].push(groups);
            }else {
              this.privatePublishForm[it].push(null);
            }
          }
        }
      }
      //
      if(this.target_columns_visible){
        for(let i=0;i<this.$global.partyNumber;i++){
          if(this.target_columns_config[i].isShow === false){
            this.privatePublishForm.target_columns.push(null);
            continue;
          }
          if(this.target_columns_all[i]){
            this.privatePublishForm.target_columns.push('all_columns');
          }else {
            this.privatePublishForm.target_columns.push(this.$global.parseInput2IntArray(this.target_columns[i]));
          }
        }
      }
      //
      if(this.scale_equiv_class_visible){
        for(let i=0;i<this.$global.partyNumber;i++){
          if(this.scale_equiv_class[i] === '-1'){
            this.privatePublishForm.scale_equiv_class.push(-1);
            continue;
          }
          if(this.scale_equiv_class[i] === '0'){
            this.privatePublishForm.scale_equiv_class.push(0);
            continue;
          }
          if(this.scale_equiv_class[i] === null || this.scale_equiv_class_config[i].isShow === false){
            this.privatePublishForm.scale_equiv_class.push(null);
            continue;
          }
          let row = this.scale_equiv_class[i].split(';');
          if(typeof(input)=='string'){
            row = [].push(row);
          }
          let secr = [];
          for(let j=0;j<row.length;j++){
            let item = row[j].split(',');
            if(typeof(item)=='string'){
              item = [].push(item);
            }
            let sec = []
            for(let k=0;k<item.length;k++){
              sec.push(parseInt(item[k]));
            }
            secr.push(sec);
          }
          this.privatePublishForm.scale_equiv_class.push(secr);
        }
      }
    },
    //
    parsePara(form){
      this.roles = form.task_role;
      this.privacy_mode = form.privacy_mode;
      this.taskRoleChange();
      for(let i=0;i<this.$global.partyNumber;i++) {
        //
        if (form.target_columns[i] === 'all_columns') {
          this.target_columns_all[i] = true;
          this.target_columns_PlaceHolder[i] = '';
        } else {
          if(form.target_columns[i]!==null){
            this.target_columns[i] = this.$global.array2str(form.target_columns[i]);
          }else{
            this.target_columns[i] = null;
          }
        }
        //
        if (form.scale_equiv_class[i] !== null) {
          if (form.scale_equiv_class[i] === -1 || form.scale_equiv_class[i] === 0) {
            this.scale_equiv_class[i] = form.scale_equiv_class[i] + '';
            continue;
          }
          this.scale_equiv_class[i] = this.$global.array2str(form.scale_equiv_class[i][0]);
          for (let j = 1; j < form.scale_equiv_class[i].length; j++) {
            this.scale_equiv_class[i] = this.scale_equiv_class[i] + ';'
                + this.$global.array2str(form.scale_equiv_class[i][j]);
          }
        } else {
          this.scale_equiv_class.push(null);
        }
      }
      //'sum_preservation_group', 'prod_preservation_group'
      this.sum_preservation_group = [];
      this.prod_preservation_group = [];
      for(let i=0;i<this.$global.partyNumber;i++) {
        //'sum_preservation_group', 'prod_preservation_group'
        let iter = ['sum_preservation_group', 'prod_preservation_group'];
        let segs = ['+', '*'];
        for (let iter_i = 0; iter_i < iter.length; iter_i++) {
          let item = iter[iter_i];
          let seg = segs[iter_i];
          if (form[item][i] === null) {
            this[item].push(null);
            continue;
          }
          let str = '';
          for (let jj = 0; jj < form[item][i].length; jj++) {
            let stri = form[item][i][jj][0];
            for (let kk = 1; kk < form[item][i][jj].length; kk++) {
              stri = stri + '=' + this.$global.array2strWithSeg(form[item][i][jj][kk], seg);
            }
            str = str + ';' + stri;
          }
          str = str.substr(1);
          this[item].push(str);
        }
      }
      this.div_consistency_group = [];
      for(let i=0;i<this.$global.partyNumber;i++) {
        if(form['div_consistency_group'][i]==null){
          this.div_consistency_group.push(null);
          continue;
        }
        let str = '';
        for(let jj=0;jj<form['div_consistency_group'][i].length;jj++){
          let stri = form['div_consistency_group'][i][jj][0] + '=';
          for(let kk=1;kk<form['div_consistency_group'][i][jj].length;kk++){
            stri = stri + form['div_consistency_group'][i][jj][kk] + '/';
          }
          stri = stri.substr(0,stri.length-1)
          str = str + ';' + stri;
        }
        str = str.substr(1);
        this.div_consistency_group.push(str);
      }
      //'privacy_level','scale_low','scale_high'
      this.privacy_level = form.privacy_level;
      this.scale_low = form.scale_low;
      this.scale_high = form.scale_high;
    },
    //
    clearForm(){
      this.privatePublishForm = {task_type:'private_publish',task_role:[],target_columns:[],scale_equiv_class:[],
        privacy_mode:[],privacy_level:[], scale_low:[], scale_high:[], sum_preservation_group:[],
        prod_preservation_group:[], div_consistency_group:[]};
      this.initPara();
      this.target_columns_visible = false;
      this.scale_equiv_class_visible =false;
      this.privacy_mode_visible =false;
      this.privacy_level_visible = false;
      this.scale_low_visible = false;
      this.scale_high_visible =false;
      this.sum_preservation_group_visible =false;
      this.prod_preservation_group_visible =false;
      this.div_consistency_group_visible =false;
    },
    //
    checkInput(name, index, para_name,checkType){
      let obj = document.getElementById(name);
      if(this.$global[checkType](this[para_name][index]) || this[para_name][index]===''){
        this[para_name+'_label'][index] = this[para_name+'_label_copy'][index];
        obj.style.borderColor = '#DCDFE6';
        return true;
      }else{
        obj.style.borderColor = '#e30d28';
        this[para_name][index] = null;
        this[para_name+'_label'][index] = "! " + this[para_name+'_label_copy'][index];
        this.$forceUpdate();
        return false;
      }
    },
    //
    changeInput(name, index, para_name,checkType){
      if(this[para_name+'_config_copy'][index].isFirstTime){
        this[para_name+'_config_copy'][index].isFirstTime = false;
      }
      this.checkInput(name, index, para_name,checkType);
    },
    //focus
    focusInput(name, index, para_name,checkType){
      let obj = document.getElementById(name);
      if(!this[para_name+'_config_copy'][index].isFirstTime && this.checkInput(name,index,para_name,checkType)){
        obj.style.borderColor = '#409EFF';
      }
    },
    //blur
    blurInput(name, index, para_name,checkType){
      let obj = document.getElementById(name);
      if(!this[para_name+'_config_copy'][index].isFirstTime && this.checkInput(name,index,para_name,checkType)){
        this[para_name+'_label'][index] = this[para_name+'_label_copy'][index];
        obj.style.borderColor = '#DCDFE6';
      }
    }
  }
}
</script>


