<template>
  <div class="k_means">
    <el-form ref="data" :model="kMeansForm" label-position="left" label-width="150px">
      <!--  max_iter -->
      <el-form-item label=":">
        <el-input v-model="max_iter" :placeholder="max_iter_label" id="max_iter"
                  @change="changeInputWithoutIndex('max_iter','max_iter','checkPositiveInteger')"
                  @focus="focusInputWithoutIndex('max_iter','max_iter','checkPositiveInteger')"
                  @blur="blurInputWithoutIndex('max_iter','max_iter','checkPositiveInteger')"
        ></el-input>
      </el-form-item>
      <!--  tol -->
      <el-form-item label=":">
        <el-input v-model="tol" :placeholder="tol_label" id="tol"
                  @change="changeInputWithoutIndex('tol','tol','checkGE0')"
                  @focus="focusInputWithoutIndex('tol','tol','checkGE0')"
                  @blur="blurInputWithoutIndex('tol','tol','checkGE0')"
        ></el-input>
      </el-form-item>
      <!-- homomorphism -->
      <el-form-item label=":">
        <el-radio-group v-model="homomorphism">
          <el-radio label="shac">shac</el-radio>
          <el-radio label="paillier">paillier</el-radio>
          <el-radio label="cpaillier">cpaillier</el-radio>
          <el-radio label="ou_hom">ou hom</el-radio>
          <el-radio label="ExpElGamal">ExpElGamal</el-radio>
          <el-radio label="real_vomo">real vomo</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--  key_size -->
      <el-form-item label=":">
        <el-input v-model="key_size" :placeholder="key_size_label" id="key_size"
                  @change="changeInputWithoutIndex('key_size','key_size','checkPositiveInteger')"
                  @focus="focusInputWithoutIndex('key_size','key_size','checkPositiveInteger')"
                  @blur="blurInputWithoutIndex('key_size','key_size','checkPositiveInteger')"
        ></el-input>
      </el-form-item>
      <!--  learning_rate -->
      <el-form-item label=":">
        <el-input v-model="learning_rate" :placeholder="learning_rate_label" id="learning_rate"
                  @change="changeInputWithoutIndex('learning_rate','learning_rate','checkL02E1')"
                  @focus="focusInputWithoutIndex('learning_rate','learning_rate','checkL02E1')"
                  @blur="blurInputWithoutIndex('learning_rate','learning_rate','checkL02E1')"
        ></el-input>
      </el-form-item>
      <!--  class_number -->
      <el-form-item label=":">
        <el-input v-model="class_number" :placeholder="class_number_label" id="class_number"
                  @change="changeInputWithoutIndex('class_number','class_number','checkGE2')"
                  @focus="focusInputWithoutIndex('class_number','class_number','checkGE2')"
                  @blur="blurInputWithoutIndex('class_number','class_number','checkGE2')"
        ></el-input>
      </el-form-item>
      <!--  batch_fraction -->
      <el-form-item label="batch:">
        <el-input v-model="batch_fraction" :placeholder="batch_fraction_label" id="batch_fraction"
                  @change="changeInputWithoutIndex('batch_fraction','batch_fraction','checkL02E1')"
                  @focus="focusInputWithoutIndex('batch_fraction','batch_fraction','checkL02E1')"
                  @blur="blurInputWithoutIndex('batch_fraction','batch_fraction','checkL02E1')"
        ></el-input>
      </el-form-item>
      <!--  random_state -->
      <el-form-item label=":">
        <el-input v-model="random_state" placeholder="[0, inf)"></el-input>
      </el-form-item>
      <!-- random_type -->
      <el-form-item label=":">
        <el-radio-group v-model="random_type">
          <el-radio label="random">random</el-radio>
          <el-radio label="roulette">roulette</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--  target_columns -->
      <el-form-item label=":">
        <el-input v-for="(item,index) in target_columns_config" :key="item.name"
                  v-model="target_columns[index]" :placeholder="target_columns_PlaceHolder[index]"
                  @change="targetColumnsChange">
          <el-switch slot="append" v-model="target_columns_all[index]" @change="targetColumnsAllChange"></el-switch>
        </el-input>
      </el-form-item>
      <el-form-item>
        <div style="float: right;">
          <el-button ref="addOrEditButton" type="primary" @click="addButton"></el-button>
          <el-button type="info" @click="closeDialog"></el-button>
          <el-button @click="clearForm"></el-button>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "KMeans",
  props: ['data'],
  mounted() {
    //
    this.$emit('setDialogWidth','45%');
    //
    this.initPara();
    this.moduleTable = this.$global.readLocalStorage('jeddak_module_table');
    //
    let temp = null;
    if(this.$global.isEdit){
      temp = this.moduleTable[this.$global.indexOfModuleTable];
      //
      this.$refs.addOrEditButton.$el.innerText = '';
    }else{
      temp = this.$global.readLocalStorage('jeddak_kmeans');
    }
    //
    if(temp !== null){
      if(temp.task_role !==null && temp.task_role.length===this.$global.partyNumber){
        this.parsePara(temp);
      }else{
        this.$notify.info({
          title: '',
          message: ''
        });
      }
    }
  },
  data() {
    return {
      moduleTableL:[],
      kMeansForm:{task_type:'kmeans', task_role:[], max_iter:[], tol:[], homomorphism:[], key_size:[], learning_rate:[],
        class_number:[], batch_fraction:[], random_state:[], random_type:[], target_columns:[]},
      //
      task_role:[],
      //
      target_columns:[],
      target_columns_config:[],
      target_columns_all:[],
      target_columns_PlaceHolder:[],
      target_columns_visible: false,
      //
      max_iter: null,max_iter_label:'[1, inf)',
      max_iter_label_copy:'[1, inf)',max_iter_time:true,
      tol: null,tol_label:'[0, inf)',tol_label_copy:'[0, inf)',tol_time:true,
      homomorphism: null,
      key_size: null,key_size_label:'[1, inf)',
      key_size_label_copy:'[1, inf)',key_size_time:true,
      learning_rate: null,learning_rate_label:'(0, 1]',
      learning_rate_label_copy:'(0, 1]',learning_rate_time:true,
      class_number: null,class_number_label:'[2, inf)',
      class_number_label_copy:'[2, inf)',class_number_time:true,
      batch_fraction: null,batch_fraction_label:'(0, 1]',
      batch_fraction_label_copy:'(0, 1]',batch_fraction_time:true,
      random_state: null,
      random_type: null,
    }
  },
  methods:{
    targetColumnsAllChange(){
      for(let i=0;i<this.$global.partyNumber;i++){
        if(this.target_columns_all[i]){
          this.target_columns[i] = null;
          this.target_columns_PlaceHolder[i] = '';
        }else {
          this.target_columns_PlaceHolder[i] = '' + (i+1) + ',,1,5; ';
        }
      }
    },
    targetColumnsChange(){
      for(let i=0;i<this.$global.partyNumber;i++){
        if(this.target_columns[i] !== null){
          this.target_columns_all[i] = false;
        }
      }
      this.$forceUpdate();
    },
    //
    closeDialog(){
      this.$emit('setVisible',false);
    },
    //
    initPara(){
      // host
      for(let i=0;i<this.$global.partyNumber;i++){
        this.task_role.push('host');
      }
      // target_columns
      let columns = this.$global.formatPartyPara(this.target_columns, this.target_columns_config,
          this.target_columns_PlaceHolder, this.$global.partyNumber,
          ',,1,5; ','col')
      this.target_columns = columns[0];
      this.target_columns_config = columns[1];
      this.target_columns_PlaceHolder = columns[2];
      this.target_columns_all = this.$global.formatList(this.target_columns_all, this.$global.partyNumber);
    },
    //
    addButton(){
      this.summarizePara();
      this.$global.writeLocalStorage('jeddak_kmeans',this.kMeansForm);
      if(this.$global.isEdit){
        this.moduleTable[this.$global.indexOfModuleTable] = this.kMeansForm;
      }else{
        this.moduleTable.push(this.kMeansForm);
      }
      this.$global.writeLocalStorage('jeddak_module_table',this.moduleTable);
      this.closeDialog();
    },
    //
    summarizePara(){
      this.kMeansForm = {task_type:'kmeans', task_role:[], max_iter:[], tol:[], homomorphism:[], key_size:[], learning_rate:[],
        class_number:[], batch_fraction:[], random_state:[], random_type:[], target_columns:[]};
      this.kMeansForm.task_role = this.task_role;
      for(let i=0;i<this.$global.partyNumber;i++){
        if(this.target_columns_all[i]){
          this.kMeansForm.target_columns.push('all_columns');
        }else {
          this.kMeansForm.target_columns.push(this.$global.parseInput2IntArray(this.target_columns[i]));
        }
      }
      for(let key in this.kMeansForm){
        if(key === 'task_type' || key === 'task_role' || key === 'target_columns'){
          continue;
        }
        if(this[key] !== null){
          if(this[key+'_visible']!==undefined && this[key+'_visible']===false){
            continue;
          }
          let keyInt = parseInt(this[key]);
          let keyFLoat = parseFloat(this[key]);
          let keyNum = keyInt;
          if(keyInt !== keyFLoat){
            keyNum = keyFLoat;
          }
          for(let i=0;i<this.$global.partyNumber;i++){
            if(isNaN(keyInt)){
              this.kMeansForm[key].push(this[key]);
            }else {
              this.kMeansForm[key].push(keyNum);
            }
          }
        }
      }
    },
    //,initPara
    parsePara(form){
      for(let key in this.kMeansForm){
        // eslint-disable-next-line no-prototype-builtins
        if(form.hasOwnProperty(key)){
          //keykey
          if(key === 'task_type'){
            continue;
          }
          if(key === 'task_role'){
            this[key] = form[key];
            continue;
          }
          if(key === 'target_columns'){
            for(let i=0;i<form[key].length;i++){
              if(form[key][i] === 'all_columns'){
                this.target_columns_all[i] = true;
                this.target_columns_PlaceHolder[i] = '';
              }else {
                this.target_columns[i] = this.$global.array2str(form[key][i]);
              }
            }
            continue;
          }
          if(form[key]!==null && form[key].length > 0){
            this[key] = form[key][0];
          }
        }
      }
    },
    //
    clearForm(){
      this.kMeansForm = {task_type:'kmeans', task_role:[], max_iter:[], tol:[], homomorphism:[], key_size:[], learning_rate:[],
        class_number:[], batch_fraction:[], random_state:[], random_type:[], target_columns:[]};
      this.initPara();
      this.max_iter= null;
      this.tol= null;
      this.homomorphism= null;
      this.key_size= null;
      this.learning_rate= null;
      this.class_number= null;
      this.batch_fraction= null;
      this.random_state= null;
      this.random_type= null;
    },
    //
    checkInputWithoutIndex(name, para_name,checkType){
      let obj = document.getElementById(name);
      if(this.$global[checkType](this[para_name]) || this[para_name]===''){
        this[para_name+'_label'] = this[para_name+'_label_copy'];
        obj.style.borderColor = '#DCDFE6';
        return true;
      }else{
        obj.style.borderColor = '#e30d28';
        this[para_name] = null;
        this[para_name+'_label'] = "! " + this[para_name+'_label_copy'];
        this.$forceUpdate();
        return false;
      }
    },
    //
    changeInputWithoutIndex(name, para_name,checkType){
      if(this[para_name+'_time']){
        this[para_name+'_time'] = false;
      }
      this.checkInputWithoutIndex(name, para_name, checkType);
    },
    //focus
    focusInputWithoutIndex(name, para_name,checkType){
      let obj = document.getElementById(name);
      if(!this[para_name+'_time'] && this.checkInputWithoutIndex(name,para_name,checkType)){
        obj.style.borderColor = '#409EFF';
      }
    },
    //blur
    blurInputWithoutIndex(name, para_name,checkType){
      let obj = document.getElementById(name);
      if(!this[para_name+'_time'] && this.checkInputWithoutIndex(name,para_name,checkType)){
        this[para_name+'_label'] = this[para_name+'_label_copy'];
        obj.style.borderColor = '#DCDFE6';
      }
    }
  }
}
</script>

