<template>
  <div class="aligner">
    <el-form ref="data" :model="alignerForm" label-position="left" label-width="150px">
      <!-- task role -->
      <el-form-item v-for="(item,index) in partyConfig" :key="item.name" :label="roleLabel[index]">
        <el-radio-group v-model="task_role[index]">
          <el-radio label="guest">guest</el-radio>
          <el-radio label="host">host</el-radio>
        </el-radio-group>
      </el-form-item>
      <!-- align mode -->
      <el-form-item label=":">
        <el-radio-group v-model="align_mode">
          <el-radio label="plain">plain</el-radio>
          <el-radio label="diffie_hellman">diffie hellman</el-radio>
          <el-radio label="cm20">cm20</el-radio>
          <el-radio label="dh_PSI">dh PSI</el-radio>
          <el-radio label="TEE">TEE</el-radio>
        </el-radio-group>
      </el-form-item>
      <!-- output_id_only -->
      <el-form-item label="ID:">
        <el-switch v-model="output_id_only" active-color="#13ce66"></el-switch>
      </el-form-item>
      <!-- sync_intersection -->
      <el-form-item label=":">
        <el-switch v-model="sync_intersection" active-color="#13ce66"></el-switch>
      </el-form-item>
      <!-- key_size -->
      <el-form-item label=":">
        <el-radio-group v-model="key_size">
          <el-radio label="1024">1024</el-radio>
          <el-radio label="2048">2048</el-radio>
          <el-radio label="3072">3072</el-radio>
          <el-radio label="4096">4096</el-radio>
        </el-radio-group>
      </el-form-item>
      <!-- batch_num -->
      <el-form-item label="batch:">
        <el-input v-model="batch_num" placeholder="[1,inf)"></el-input>
      </el-form-item>
      <el-form-item>
        <div style="float: right;">
          <el-button ref="addOrEditButton" type="primary" @click="addButton"></el-button>
          <el-button type="info" @click="closeDialog"></el-button>
          <el-button @click="clearForm"></el-button>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "Aligner",
  props: ['data'],
  mounted() {
    //
    this.$emit('setDialogWidth','45%');
    //
    this.initPara();
    this.moduleTable = this.$global.readLocalStorage('jeddak_module_table');
    //
    let temp = null;
    if(this.$global.isEdit){
      temp = this.moduleTable[this.$global.indexOfModuleTable];
      //
      this.$refs.addOrEditButton.$el.innerText = '';
    }else{
      temp = this.$global.readLocalStorage('jeddak_aligner');
    }
    //
    if(temp !== null){
      if(temp.task_role !==null && temp.task_role.length===this.$global.partyNumber){
        this.parsePara(temp);
      }else{
        this.$notify.info({
          title: '',
          message: ''
        });
      }
    }
  },
  data() {
    return {
      moduleTableL:[],
      align_mode: null,
      output_id_only: null,
      sync_intersection:null,
      key_size:null,
      batch_num:null,
      alignerForm:{task_type: 'aligner',task_role:[],align_mode:[],output_id_only:[],sync_intersection:[],key_size:[],
        batch_num:[]},
      role_data:[{key:1,label:'guest'},
        {key:2,label:'host'}],
      role_value:['guest','host'],
      task_role:[],
      partyConfig:[],
      roleLabel:[]
    };
  },
  methods: {
    //
    closeDialog(){
      this.$emit('setVisible',false);
    },
    //
    initPara(){
      //
      let role = this.$global.formatPartyPara(this.task_role, this.partyConfig, this.roleLabel,
          this.$global.partyNumber, ':', 'role')
      this.task_role = role[0];
      this.partyConfig = role[1];
      this.roleLabel = role[2];
      this.$forceUpdate();
    },
    //
    addButton(){
      this.summarizePara();
      this.$global.writeLocalStorage('jeddak_aligner',this.alignerForm);
      if(this.$global.isEdit){
        this.moduleTable[this.$global.indexOfModuleTable] = this.alignerForm;
      }else{
        this.moduleTable.push(this.alignerForm);
      }
      this.$global.writeLocalStorage('jeddak_module_table',this.moduleTable);
      this.closeDialog();
    },
    //
    summarizePara(){
      this.alignerForm = {task_type: 'aligner',task_role:[],align_mode:[],output_id_only:[],
        sync_intersection:[],key_size:[],batch_num:[]};
      this.alignerForm.task_role = this.task_role;
      for(let i=0;i<this.$global.partyNumber;i++){
        this.alignerForm.align_mode.push(this.align_mode);
        this.alignerForm.output_id_only.push(this.output_id_only);
        this.alignerForm.sync_intersection.push(this.sync_intersection);
        this.alignerForm.key_size.push(parseInt(this.key_size));
        if(this.batch_num!==null || this.batch_num!==''){
          this.alignerForm.batch_num.push(parseInt(this.batch_num));
        }
      }
    },
    //,initPara
    parsePara(form){
      for(let key in this.alignerForm){
        // eslint-disable-next-line no-prototype-builtins
        if(form.hasOwnProperty(key)){
          //keykey
          if(key === 'task_type'){
            continue;
          }
          if(key === 'align_mode' || key === 'output_id_only' || key === 'sync_intersection'
              || key === 'key_size' || key === 'batch_num'){
            if(form[key]!== null && form[key].length>0){
              this[key] = form[key][0];
              if(key === 'key_size'){
                this[key] = this[key] + '';
              }
            }
          }else if(form[key]!==null && this[key].length === form[key].length){
            this[key] = form[key];
          }
        }
      }
    },
    //
    clearForm(){
      this.alignerForm = {task_type: 'aligner',task_role:[],align_mode:[],output_id_only:[],
        sync_intersection:[],key_size:[],batch_num:[]};
      this.initPara();
      this.align_mode=null;
      this.output_id_only=null;
      this.sync_intersection=null;
      this.key_size=null;
    },
  }
}
</script>

