<template>
  <div class="one_hot_encoder">
    <el-form ref="data" :model="oneHotEncoderForm">
      <!-- task role -->
      <el-form-item v-for="(item,index) in roleConfig" :key="item.name" :label="roleLabel[index]">
        <el-radio-group v-model="task_role[index]">
          <el-radio label="1">guest</el-radio>
          <el-radio label="2">host</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--  target_columns -->
      <el-form-item label=":">
        <el-input v-for="(item,index) in target_columns_config" :key="item.name" v-model="target_columns[index]"
                  :placeholder="target_columns_PlaceHolder[index]" @change="targetColumnsChange">
          <el-switch slot="append" v-model="target_columns_all[index]" @change="targetColumnsAllChange"></el-switch>
        </el-input>
      </el-form-item>
      <!--   reserve   -->
      <el-form-item label=":">
        <el-switch v-model="reserve" active-color="#13ce66"></el-switch>
      </el-form-item>
      <el-form-item>
        <div style="float: right;">
          <el-button ref="addOrEditButton" type="primary" @click="addButton"></el-button>
          <el-button type="info" @click="closeDialog"></el-button>
          <el-button @click="clearForm"></el-button>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "OneHotEncoder",
  props: ['data'],
  mounted() {
    //
    this.$emit('setDialogWidth','30%');
    //
    this.initPara();
    this.moduleTable = this.$global.readLocalStorage('jeddak_module_table');
    //
    let temp = null;
    if(this.$global.isEdit){
      temp = this.moduleTable[this.$global.indexOfModuleTable];
      //
      this.$refs.addOrEditButton.$el.innerText = '';
    }else{
      temp = this.$global.readLocalStorage('jeddak_onehotencoder');
    }
    //
    if(temp !== null){
      if(temp.task_role !==null && temp.task_role.length===this.$global.partyNumber){
        this.parsePara(temp);
      }else{
        this.$notify.info({
          title: '',
          message: ''
        });
      }
    }
  },
  data() {
    return {
      moduleTableL:[],
      oneHotEncoderForm:{task_type:'one_hot_encoder',task_role:[],target_columns:[],reserve:false},
      //
      role_data:[{key:1,label:'guest'},
        {key:2,label:'host'}],
      role_value:['guest','host'],
      task_role:[],
      roleConfig:[],
      roleLabel:[],
      //
      target_columns:[],
      target_columns_config:[],
      target_columns_all:[],
      target_columns_PlaceHolder:[],
      // reserve
      reserve:false,
    };
  },
  methods: {
    targetColumnsAllChange(){
      for(let i=0;i<this.$global.partyNumber;i++){
        if(this.target_columns_all[i]){
          this.target_columns[i] = null;
          this.target_columns_PlaceHolder[i] = '';
        }else {
          this.target_columns_PlaceHolder[i] = '' + (i+1) + ',,1,5; ';
        }
      }
    },
    targetColumnsChange(){
      for(let i=0;i<this.$global.isAdmin;i++){
        this.target_columns_all[i] = false;
      }
      this.$forceUpdate();
    },
    //
    closeDialog(){
      this.$emit('setVisible',false);
    },
    //
    initPara(){
      // 
      let role = this.$global.formatPartyPara(this.task_role, this.roleConfig, this.roleLabel,
          this.$global.partyNumber, ':','role')
      this.task_role = role[0];
      this.roleConfig = role[1];
      this.roleLabel = role[2];
      // target_columns
      let columns = this.$global.formatPartyPara(this.target_columns, this.target_columns_config,
          this.target_columns_PlaceHolder, this.$global.partyNumber, ',,1,5; ','col')
      this.target_columns = columns[0];
      this.target_columns_config = columns[1];
      this.target_columns_PlaceHolder = columns[2];
      this.target_columns_all = this.$global.formatList(this.target_columns_all, this.$global.partyNumber);
    },
    //
    addButton(){
      this.summarizePara();
      this.$global.writeLocalStorage('jeddak_onehotencoder',this.oneHotEncoderForm);
      if(this.$global.isEdit){
        this.moduleTable[this.$global.indexOfModuleTable] = this.oneHotEncoderForm;
      }else{
        this.moduleTable.push(this.oneHotEncoderForm);
      }
      this.$global.writeLocalStorage('jeddak_module_table',this.moduleTable);
      this.closeDialog();
    },
    //
    summarizePara(){
      this.oneHotEncoderForm = {task_type:'one_hot_encoder',task_role:[],target_columns:[],reserve:false};
      this.oneHotEncoderForm.task_role = this.task_role;
      for(let i=0;i<this.$global.partyNumber;i++){
        if(this.target_columns_all[i]){
          this.oneHotEncoderForm.target_columns.push('all_columns');
        }else {
          this.oneHotEncoderForm.target_columns.push(this.$global.parseInput2IntArray(this.target_columns[i]));
        }
      }
      this.oneHotEncoderForm.reserve = this.reserve;
    },
    //,initPara
    parsePara(form){
      for(let key in this.oneHotEncoderForm){
        // eslint-disable-next-line no-prototype-builtins
        if(form.hasOwnProperty(key)){
          //keykey
          if(key === 'task_type'){
            continue;
          }
          if(key === 'target_columns'){
            for(let i=0;i<form[key].length;i++){
              if(form[key][i] === 'all_columns'){
                this.target_columns_all[i] = true;
                this.target_columns_PlaceHolder[i] = '';
              }else {
                this.target_columns[i] = this.$global.array2str(form[key][i]);
              }
            }
          }else if(form[key]!==null && this[key].length === form[key].length){
            this[key] = form[key];
          }
        }
      }
    },
    //
    clearForm(){
      this.oneHotEncoderForm = {task_type:'one_hot_encoder',task_role:[],target_columns:[],reserve:false};
      this.initPara();
      this.reserve = false;
    },
  }
}
</script>

